.card{
    position:relative;
    
}
.card img{
    width: 100%;
    display:block;
    border:2px solid #e4552a;
    border-radius: 3px;
}

.front:hover{
    
  border: black;
}
.back:hover{
    
 border:black;
}
/* .....front of card ---*/
.card .front{
 transform: rotateY(90deg);
 transition: all ease-in 0.2s;
 position:absolute;
}
.flipped .front{
    transform: rotateY(0deg);
    transition-delay: 0.2s;
}

/*back of the card ....*/
.card .back{
    transition: all ease-in 0.2s;
    transition-delay: 0.2s;
}
.flipped .back{
    transform: rotateY(90deg);
    transition-delay: 0s;
}
.btnSide{
  position: absolute; 
  left: 70%; 
  text-align: center;
  width: 20%;
}